
import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { selectUserInfo, updateUserAsync } from '../UserSlice';
import { useForm } from 'react-hook-form';


export function UserProfile() {
    const dispatch = useDispatch();
    const userInfo = useSelector(selectUserInfo)
    const [selectedEditIndex, setSelectedEditIndex] = useState(-1);
    const [showAddAddressForm, setShowAddAddressForm] = useState(false);




    console.log(selectedEditIndex);

    const { register, handleSubmit, setValue, reset, formState: { errors } } = useForm();



    const handleEdit = (addressUpdate, index) => {
        const newUser = { ...userInfo, addresses: [...userInfo.addresses] };
        newUser.addresses.splice(index, 1, addressUpdate);
        dispatch(updateUserAsync(newUser));
        setSelectedEditIndex(-1);
    }

    const handleRemove = (e, index) => {
        const newUser = { ...userInfo, addresses: [...userInfo.addresses] }
        newUser.addresses.splice(index, 1);
        dispatch(updateUserAsync(newUser))

    }

    const handleEditForm = (index) => {
        setSelectedEditIndex(index);
        const address = userInfo.addresses[index];
        setValue('name', address.name)
        setValue('email', address.email)
        setValue('phone', address.phone)
        setValue('street', address.street)
        setValue('city', address.city)
        setValue('state', address.state)
        setValue('pinCode', address.pinCode)
    }

    const handleAdd = (address) => {
        const newUser = { ...userInfo, addresses: [...userInfo.addresses, address] };
        dispatch(updateUserAsync(newUser));
        setShowAddAddressForm(false);
    }



    return (
        <>
            <div>
                < div className="mx-auto max-w-7xl mt-12 p-4 sm:px-6 lg:px-8 bg-white">
                    <h1 className="text-4xl font-bold tracking-tight text-gray-900"> Name: {userInfo.name ? userInfo.name : 'New User'}</h1>
                    <h3 className="text-2xl font-bold tracking-tight text-green-500"> Email : {userInfo.email} </h3>

                  {userInfo.role === 'admin' &&  <h3 className="text-2xl font-bold tracking-tight text-red-400 m-6"> Role : {userInfo.role} </h3>}







                    <div className="flex justify-between gap-x-6 py-5">
                        <div className="flex gap-x-4">
                            <div>
                                <button
                                    onClick={(e) => { setShowAddAddressForm(true); setSelectedEditIndex(-1) }}
                                    type="button"
                                    className="rounded-md my-5 bg-green-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-green-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                                >
                                    Add New Address
                                </button>

                                {showAddAddressForm ?
                                    <form className='bg-white mt-24 w-100' noValidate
                                        onSubmit={handleSubmit((data) => {

                                            handleAdd(data)
                                            reset();
                                        })}>


                                        <div className="space-y-12">

                                            <div className="border-b border-gray-900/10 pb-12">

                                                <h2 className="text-4xl font-bold tracking-tight text-gray-900 p-4">Personal Information</h2>
                                                <p className="mt-1 text-sm leading-6 text-gray-600">Use a permanent address where you can receive mail.</p>

                                                <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                                                    <div className="sm:col-span-3">
                                                        <label htmlFor="name" className="block text-sm font-medium leading-6 text-gray-900">
                                                            Full Name
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                type="text"
                                                                {...register('name', { required: 'Name is required' })}
                                                                id="name"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                            />
                                                        </div>
                                                    </div>

                                                    <div className="sm:col-span-4">
                                                        <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
                                                            Email address
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                id="email"
                                                                {...register('email', { required: 'Email is required' })}
                                                                type="email"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                            />
                                                        </div>
                                                    </div>

                                                    <div className="sm:col-span-3">
                                                        <label htmlFor="phone" className="block text-sm font-medium leading-6 text-gray-900">
                                                            Phone
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                id="phone"
                                                                type='tel'
                                                                {...register('phone', { required: 'Phone is required' })}
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                            />
                                                        </div>
                                                    </div>

                                                    <div className="col-span-full">
                                                        <label htmlFor="street-address" className="block text-sm font-medium leading-6 text-gray-900">
                                                            Street address
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                type="text"
                                                                {...register('street', { required: 'Street address is required' })}
                                                                id="street"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                            />
                                                        </div>
                                                    </div>

                                                    <div className="sm:col-span-2 sm:col-start-1">
                                                        <label htmlFor="city" className="block text-sm font-medium leading-6 text-gray-900">
                                                            City
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                type="text"
                                                                {...register('city', { required: 'City is required' })}
                                                                id="city"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                            />
                                                        </div>
                                                    </div>

                                                    <div className="sm:col-span-2">
                                                        <label htmlFor="state" className="block text-sm font-medium leading-6 text-gray-900">
                                                            State / Province
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                type="text"
                                                                {...register('state', { required: 'State is required' })}
                                                                id="state"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                            />
                                                        </div>
                                                    </div>

                                                    <div className="sm:col-span-2">
                                                        <label htmlFor="pinCode" className="block text-sm font-medium leading-6 text-gray-900">
                                                            ZIP / Postal/ PIN code
                                                        </label>
                                                        <div className="mt-2">
                                                            <input
                                                                type="number"
                                                                {...register('pinCode', { required: 'PIN code address is required' })}
                                                                id="pinCode"
                                                                className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                            />
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="mt-6 flex items-center justify-end gap-x-6">


                                                <button
                                                    // onClick={(e) => setSelectedEditIndex(index)}
                                                    type="submit"
                                                    className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                                                >
                                                    Add Address
                                                </button>
                                            </div>

                                        </div>


                                    </form> : null}

                                <p className="text-2xl font-semibold leading-6 text-gray-900">Your Addresses: </p>
                                {userInfo.addresses  && userInfo.addresses.map((address, index) =>

                                    <>
                                        <div >
                                            {selectedEditIndex === index ?
                                                <form className='bg-white mt-24 w-100' noValidate
                                                    onSubmit={handleSubmit((data) => {

                                                        handleEdit(data, index)
                                                        // console.log(data);
                                                        // dispatch(
                                                        //     updateUserAsync({ ...user, addresses: [...user.addresses, data] })
                                                        // )
                                                        reset();
                                                    })}>


                                                    <div className="space-y-12">

                                                        <div className="border-b border-gray-900/10 pb-12">

                                                            <h2 className="text-4xl font-bold tracking-tight text-gray-900 p-4">Personal Information</h2>
                                                            <p className="mt-1 text-sm leading-6 text-gray-600">Use a permanent address where you can receive mail.</p>

                                                            <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                                                                <div className="sm:col-span-3">
                                                                    <label htmlFor="name" className="block text-sm font-medium leading-6 text-gray-900">
                                                                        Full Name
                                                                    </label>
                                                                    <div className="mt-2">
                                                                        <input
                                                                            type="text"
                                                                            {...register('name', { required: 'Name is required' })}
                                                                            id="name"
                                                                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                                        />
                                                                    </div>
                                                                </div>

                                                                <div className="sm:col-span-4">
                                                                    <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
                                                                        Email address
                                                                    </label>
                                                                    <div className="mt-2">
                                                                        <input
                                                                            id="email"
                                                                            {...register('email', { required: 'Email is required' })}
                                                                            type="email"
                                                                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                                        />
                                                                    </div>
                                                                </div>

                                                                <div className="sm:col-span-3">
                                                                    <label htmlFor="phone" className="block text-sm font-medium leading-6 text-gray-900">
                                                                        Phone
                                                                    </label>
                                                                    <div className="mt-2">
                                                                        <input
                                                                            id="phone"
                                                                            type='tel'
                                                                            {...register('phone', { required: 'Phone is required' })}
                                                                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                                        />
                                                                    </div>
                                                                </div>

                                                                <div className="col-span-full">
                                                                    <label htmlFor="street-address" className="block text-sm font-medium leading-6 text-gray-900">
                                                                        Street address
                                                                    </label>
                                                                    <div className="mt-2">
                                                                        <input
                                                                            type="text"
                                                                            {...register('street', { required: 'Street address is required' })}
                                                                            id="street"
                                                                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                                        />
                                                                    </div>
                                                                </div>

                                                                <div className="sm:col-span-2 sm:col-start-1">
                                                                    <label htmlFor="city" className="block text-sm font-medium leading-6 text-gray-900">
                                                                        City
                                                                    </label>
                                                                    <div className="mt-2">
                                                                        <input
                                                                            type="text"
                                                                            {...register('city', { required: 'City is required' })}
                                                                            id="city"
                                                                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                                        />
                                                                    </div>
                                                                </div>

                                                                <div className="sm:col-span-2">
                                                                    <label htmlFor="state" className="block text-sm font-medium leading-6 text-gray-900">
                                                                        State / Province
                                                                    </label>
                                                                    <div className="mt-2">
                                                                        <input
                                                                            type="text"
                                                                            {...register('state', { required: 'State is required' })}
                                                                            id="state"
                                                                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                                        />
                                                                    </div>
                                                                </div>

                                                                <div className="sm:col-span-2">
                                                                    <label htmlFor="pinCode" className="block text-sm font-medium leading-6 text-gray-900">
                                                                        ZIP / Postal/ PIN code
                                                                    </label>
                                                                    <div className="mt-2">
                                                                        <input
                                                                            type="number"
                                                                            {...register('pinCode', { required: 'PIN code address is required' })}
                                                                            id="pinCode"
                                                                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                                        />
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className="mt-6 flex items-center justify-end gap-x-6">
                                                            <button
                                                                onClick={(e) => setSelectedEditIndex(-1)}
                                                                type="submit"
                                                                className="rounded-md bg-red-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-red-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                                                            >
                                                                Cancel
                                                            </button>

                                                            <button
                                                                // onClick={(e) => setSelectedEditIndex(index)}
                                                                type="submit"
                                                                className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                                                            >
                                                                Edit Address
                                                            </button>
                                                        </div>

                                                    </div>


                                                </form> : null}





                                            <ul role="list" className="divide-y divide-gray-100">
                                                <li
                                                    className='flex justify-between w-96 gap-x-6 px-5 py-5 my-6 '
                                                >
                                                    <div className="flex min-w-0 gap-x-4">
                                                        <div className="min-w-0 flex-auto">
                                                            <p className="text-sm font-semibold leading-6 text-gray-900">{address.name}</p>
                                                            <p className="mt-1 truncate text-xs leading-5 text-gray-500">{address.street}, {address.city}, {address.state}</p>
                                                        </div>
                                                    </div>
                                                    <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                                                        <p className="mt-1 truncate text-xs leading-5 text-gray-500">Pin Code: {address.pinCode}.</p>
                                                        <p className="text-sm leading-6 text-gray-900">{address.phone}</p>

                                                    </div>

                                                    <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                                                        <button

                                                            onClick={(e) => { handleEditForm(index); setShowAddAddressForm(false) }}
                                                            type="button"
                                                            className="font-medium text-indigo-600 hover:text-indigo-500"
                                                        >
                                                            Edit
                                                        </button>

                                                        <button
                                                            onClick={(e) => handleRemove(e, index)}
                                                            type="button"
                                                            className="font-medium text-indigo-600 hover:text-indigo-500"
                                                        >
                                                            Remove
                                                        </button>

                                                    </div>
                                                </li>

                                            </ul>
                                        </div>
                                    </>
                                )}
                            </div>
                        </div>
                    </div>



                </div >
            </div>
        </>
    );
}
