import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { ITEM_PER_PAGE, discountedPrice } from "../../../app/constant";
import {
  fetchAllOrdersAsync,
  selectOrders,
  selectTotalOrders,
  updateOrderAsync,
} from "../../order/orderSlice";
import { PencilIcon, EyeIcon, ArrowDownIcon, ArrowUpIcon } from "@heroicons/react/24/outline";
import Pagination from "../../common/Pagination";

const AdminOrders = () => {
  const dispatch = useDispatch();
  const [page, setPage] = useState(1);
  const [sort, setSort] = useState({});
  const [editableOrderId, setEditableOrderId] = useState(-1);
  const orders = useSelector(selectOrders);
  console.log(orders);
  const totalOrders = useSelector(selectTotalOrders);

  const handleEdit = (order) => {
    setEditableOrderId(order.id);
  };

  const handleShow = () => {};

  const handleUpdate = (e, order) => {
    const updatedOrder = { ...order, status: e.target.value };
    dispatch(updateOrderAsync(updatedOrder));
    setEditableOrderId(-1);
  };

  const handlePage = (page) => {
    setPage(page);
    
  };

  const chooseColor = (status) => {
    switch (status) {
      case "pending":
        return "bg-purple-200 text-purple-600";

      case "dispatched":
        return "bg-yellow-200 text-yellow-600";

      case "delivered":
        return "bg-green-200 text-green-600";

      case "cancelled":
        return "bg-red-200 text-red-600";
      default:
        return "bg-purple-200 text-purple-600";
    }
  };

  const handleSort = (sortOption) => {

    const sort = { _sort: sortOption.sort, _order: sortOption.order };

    setSort(sort);
  };

  useEffect(() => {
    const pagination = { _page: page, _limit: ITEM_PER_PAGE };
    dispatch(fetchAllOrdersAsync({ sort, pagination }));
  }, [dispatch, page, sort]);

  return (
    <>
      <div className="container m-auto">
        <div className=" bg-gray-100 flex items-center justify-center font-sans ">
          <div className=" ">
            <div className="bg-white shadow-md rounded my-6">
              <table className="min-w-max w-full table-auto">
                <thead>
                  <tr className="bg-gray-200 cursor-pointer text-gray-600 uppercase text-sm leading-normal">
                    <th
                      className="py-3 px-6 text-left"
                      onClick={(e) =>
                        handleSort({
                          sort: "id",
                          order: sort?._order === "asc" ? "desc" : "asc",
                        })
                      }
                    >
                      Order# { sort._sort === 'id' && ( sort._order === 'asc' ?
                        <ArrowUpIcon className="w-4 h-4 inline mx-2" /> :
                        <ArrowDownIcon className="w-4 inline mx-2 h-4" />
                        )}
                    </th>
                    <th className="py-3 px-6 text-left">Items</th>
                    <th className="py-3 px-6 text-left">price</th>
                    <th
                      className="py-3 px-6 text-left"
                      onClick={(e) =>
                        handleSort({
                          sort: "totalAmount",
                          order: sort?._order === "asc" ? "desc" : "asc",
                        })
                      }
                    >
                      Total Amount { sort._sort === 'totalAmount' && ( sort._order === 'asc' ?
                        <ArrowUpIcon className="w-4 h-4 inline mx-2" /> :
                        <ArrowDownIcon className="w-4 inline mx-2 h-4" />
                        )}
                    </th>
                    <th className="py-3 px-6 text-center">Shipping Address </th>
                    <th className="py-3 px-6 text-center">Status</th>
                    <th className="py-3 px-6 text-center">Actions</th>
                  </tr>
                </thead>
                <tbody className="text-gray-600 text-sm font-light">
                  {orders &&
                    orders.map((order) => (
                      <tr className="border-b border-gray-200 hover:bg-gray-100">
                        <td className="py-3 px-6 text-left whitespace-nowrap">
                          <div className="flex items-center">
                            <div className="mr-2"></div>
                            <span className="font-medium">{order.id}</span>
                          </div>
                        </td>

                        <td className="py-3 px-6 text-left">
                          {order.items &&
                            order.items.map((item, index) => (
                              <div key={index} className="flex items-center">
                                <div className="mr-2">
                                  <img
                                    className="w-6 h-6 rounded-full"
                                    src={item.product.thumbnail}
                                    alt={item.product.title}
                                  />
                                </div>
                                <span className="font-semibold">
                                  {item.product.title}: Qty {item.quantity}{" "}
                                </span>
                              </div>
                            ))}
                        </td>

                        <td className="py-3 px-6 text-left">
                          {order.items &&
                            order.items.map((item) => (
                              <div className="flex items-center">
                                <span className="font-semibold">
                                  ${discountedPrice(item.product)}{" "}
                                </span>
                              </div>
                            ))}
                        </td>

                        <td className="py-3 px-6 text-center">
                          <div className="flex font-semibold text-black items-center justify-center">
                            ${order.totalAmount}
                          </div>
                        </td>

                        <td className="py-3 px-6 text-center">
                          <div className="flex font-semibold text-black items-center justify-center">
                            <div>
                              <div> {order.selectedAddress.name} </div>
                              <div> {order.selectedAddress.email} </div>
                              <div> {order.selectedAddress.phone} </div>
                              <div>
                                {" "}
                                {order.selectedAddress.street},{" "}
                                {order.selectedAddress.city},{" "}
                                {order.selectedAddress.state}{" "}
                              </div>
                              <div> {order.selectedAddress.pinCode} </div>
                            </div>
                          </div>
                        </td>

                        <td className="py-3 px-6 text-center">
                          {order.id === editableOrderId ? (
                            <select onChange={(e) => handleUpdate(e, order)}>
                              <option value="pending">Panding</option>
                              <option value="dispatched">Dispatched</option>
                              <option value="delivered">Delivered</option>
                              <option value="cancelled">Cancelled</option>
                            </select>
                          ) : (
                            <span
                              className={`${chooseColor(
                                order.status
                              )} py-1 px-3 rounded-full font-semibold text-xs`}
                            >
                              {order.status}
                            </span>
                          )}
                        </td>

                        <td className="py-3 px-6 text-center">
                          <div className="flex item-center justify-center">
                            <div className="w-4 mx-2 transform hover:text-purple-500 hover:scale-110">
                              <EyeIcon
                                className="w-7 h-7"
                                onClick={(e) => handleShow(order)}
                              />
                            </div>
                            <div className="w-4 mx-2 transform hover:text-purple-500 hover:scale-110">
                              <PencilIcon
                                className="w-6 h-6"
                                onClick={(e) => handleEdit(order)}
                              />
                            </div>
                            <div className="w-4 mx-2 transform hover:text-purple-500 hover:scale-110">
                              {/* <EyeIcon className='w-7 h-7' /> */}
                            </div>
                          </div>
                        </td>
                      </tr>
                    ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <Pagination
          handlePage={handlePage}
          page={page}
          setPage={setPage}
          totalItems={totalOrders}
        />
      </div>
    </>
  );
};

export default AdminOrders;
